import Card from 'components/ui/Card';
import Typography from 'components/ui/Typography';
import Chip from 'components/ui/Chip';

interface PortfolioCardProps {
  title: string;
  content: string;
  tags: string[];
  label: '공격형' | '중립형' | '안정형';
  imageName?: string;
  onClick?: () => void;
}

const COLOR_MAPS: { [key in PortfolioCardProps['label']]: 'red' | 'green' | 'blue' } = {
  공격형: 'red',
  중립형: 'green',
  안정형: 'blue'
};

const PortfolioCard = ({ title, content, tags, label, imageName, onClick }: PortfolioCardProps) => {
  return (
    <Card hasShadow px="px-5" py="py-5" onClick={onClick}>
      <div className="flex flex-col justify-between gap-3 w-full">
        <div className="flex items-start w-full gap-3">
          <div className="flex flex-col grow gap-[0.625rem]">
            <Typography size="lg" weight={700} color="text-gray-900" className="hover:underline">
              {title}
            </Typography>

            <div className="flex min-h-[4.6875rem]">
              <Typography size="sm" color="text-gray-800">
                {content}
              </Typography>
            </div>
          </div>
          <img
            src={`/assets/icons/portfolio-card/${imageName}`}
            alt={`${title}`}
            className="inline-flex items-center justify-center shrink-0 w-14 h-14 text-caption text-gray-600"
          />
        </div>
        <div className="flex items-end w-full">
          <div className="flex flex-wrap gap-y-2">
            {tags.map((tag, index) => (
              <Chip
                key={index}
                variant="ghost"
                size="sm"
                prefix={
                  <Typography variant="caption" color="text-gray-600/40">
                    #
                  </Typography>
                }
                label={tag}
              />
            ))}
          </div>
          <div className="inline-flex shrink-0 ml-auto">
            <Chip color={COLOR_MAPS[label]} label={label} variant="outlined" />
          </div>
        </div>
      </div>
    </Card>
  );
};

export default PortfolioCard;
